html {
  -webkit-tap-highlight-color: transparent; }

.container {
  width: 375px;
  height: 812px;
  background-color: black;
  margin: 0px auto;
  position: relative; }

.inputCalc {
  top: 220px;
  position: absolute;
  box-sizing: border-box;
  width: 375px;
  height: 80px;
  background-color: transparent;
  color: white;
  font-size: 80px;
  text-align: right;
  border: none;
  outline: none;
  border: none;
  padding: 0px 22px; }

.grid-container {
  display: grid;
  position: absolute;
  width: 331px;
  left: 22px;
  bottom: 58px;
  grid-template-areas: "AC plusMinus remain divide" "seven eight nine multiply" "four five six minus" "one two three plus" "zero zero point equal";
  grid-row-gap: 21px;
  grid-column-gap: 21px;
  padding: 5px; }
  .grid-container .grid-item {
    background-color: #333333;
    border-radius: 100%;
    width: 67px;
    height: 67px;
    border: none;
    cursor: pointer; }
    .grid-container .grid-item:active {
      background-color: #222222; }
  .grid-container .AC, .grid-container .plusMinus, .grid-container .remain {
    background-color: #a5a5a5; }
    .grid-container .AC:active, .grid-container .plusMinus:active, .grid-container .remain:active {
      background-color: #6b6b6b; }
  .grid-container .zero {
    width: 156px;
    border-radius: calc(67px / 2); }
  .grid-container .plus, .grid-container .minus, .grid-container .divide, .grid-container .equal, .grid-container .multiply {
    background-color: #21751e; }
  .grid-container .plus:active, .grid-container .minus:active, .grid-container .divide:active, .grid-container .equal:active, .grid-container .multiply:active {
    background-color: #a76400; }

